import React from "react"
import { Link, graphql } from "gatsby"
import { css } from "@emotion/core"
import { rhythm } from "../utils/typography"
import Layout from "../components/layout"
import { Helmet } from "react-helmet"
import { Flex, Box} from "@rebass/grid/"

export default ({ data }) => {
    console.log(data)
    return (
        <Layout>
            <Flex p={5} flexWrap='wrap' mt={1} width={1}>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>{data.site.siteMetadata.title} | Portofolio</title>
                    <link rel="" target="_blank" href="http://twitter.com/positivneutron" />
                </Helmet>
                {data.allSrcJson.edges[0].node.portofolio.map((node, index) => (
                <Box width={1}>
                    <div dangerouslySetInnerHTML={{ __html: node.site }} />
                    <p>
                        {node.description}
                    </p>
                </Box>
                ))}
            </Flex>
        </Layout>
    )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        title        
      }
    }
    allSrcJson{
    edges{
      node{
        portofolio{
            site
            description
        }
      }
    }
  }
  }
`